*{
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.app{
  padding: 1.5rem;
  background-color: rgb(50,50,103);
}

.movies{
  display: flex;
  flex-wrap: wrap;
}

.search_nav{
  display: flex;
  align-items: center;
  padding-bottom: 1.5rem;
}
.title{
  padding-right: 3rem;
}

.title h1{
  color: white;
}

.search_box input{
  border: 0;
  outline: 0;
  margin-right: 0.2rem;
  border-radius: 3rem;
  height: 1rem;
  padding: 7px;
  
}
.search_box button{
  border: 0;
  outline: 0;
  background-color: white;  
  padding: 0.5rem;
  border-radius: 1rem;
  margin-left: 0.5rem;
}